// src/pages/MandiRates.jsx
import React, { useEffect, useState } from "react";
import axios from "axios";
import { Modal, Button } from "react-bootstrap";
import { Line } from "react-chartjs-2";
import "chart.js/auto";

const MandiRates = () => {
  const [mandiData, setMandiData] = useState([]);
  const [stateList, setStateList] = useState([]);
  const [districtList, setDistrictList] = useState([]);
  const [commodityList, setCommodityList] = useState([]);

  const [selectedState, setSelectedState] = useState("");
  const [selectedDistrict, setSelectedDistrict] = useState("");
  const [selectedCommodity, setSelectedCommodity] = useState("");

  const [loading, setLoading] = useState(false);
  const [bestMandi, setBestMandi] = useState(null);

  const [showModal, setShowModal] = useState(false);
  const [trendData, setTrendData] = useState({ history: [], prediction: [] });

  const fetchMandiRates = async (state = "", district = "", commodity = "") => {
    setLoading(true);
    try {
      let url = "http://localhost:8001/mandi-rates";
      const params = new URLSearchParams();
      if (state) params.append("state", state);
      if (district) params.append("district", district);
      if (commodity) params.append("commodity", commodity);

      if (params.toString()) url += `?${params.toString()}`;
      const res = await axios.get(url);
      const records = res.data.records || [];

      setMandiData(records);

      const uniqueStates = [...new Set(records.map((item) => item.state))];
      const uniqueDistricts = [
        ...new Set(records.map((item) => item.district)),
      ];
      const uniqueCommodities = [
        ...new Set(records.map((item) => item.commodity)),
      ];

      setStateList(uniqueStates);
      setDistrictList(uniqueDistricts);
      setCommodityList(uniqueCommodities);

      if (commodity && records.length > 0) {
        const best = records.reduce((a, b) =>
          (parseFloat(a.modal_price) || 0) > (parseFloat(b.modal_price) || 0)
            ? a
            : b
        );
        setBestMandi(best);
      } else {
        setBestMandi(null);
      }
    } catch (err) {
      console.error("Error fetching mandi rates:", err);
      setMandiData([]);
      setBestMandi(null);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchMandiRates();
  }, []);

  const handleSearch = () => {
    fetchMandiRates(selectedState, selectedDistrict, selectedCommodity);
  };

  const handleTrendClick = async (state, market, commodity) => {
    try {
      const res = await axios.get(`http://localhost:8001/predict-price-trend`, {
        params: { state, market, commodity },
      });
      setTrendData(res.data);
      setShowModal(true);
    } catch (err) {
      console.error("Trend fetch failed:", err);
      alert("Unable to fetch trend data.");
    }
  };

  return (
    <div className="container py-4">
      <h2 className="text-center mb-4 text-success fw-bold">🌾 Mandi Rates</h2>

      {/* Filters */}
      <div className="row g-3 mb-4">
        <div className="col-md-4">
          <select
            className="form-select"
            value={selectedState}
            onChange={(e) => setSelectedState(e.target.value)}
          >
            <option value="">Select State</option>
            {stateList.map((state) => (
              <option key={state} value={state}>
                {state}
              </option>
            ))}
          </select>
        </div>

        <div className="col-md-4">
          <select
            className="form-select"
            value={selectedDistrict}
            onChange={(e) => setSelectedDistrict(e.target.value)}
          >
            <option value="">Select District</option>
            {districtList.map((district) => (
              <option key={district} value={district}>
                {district}
              </option>
            ))}
          </select>
        </div>

        <div className="col-md-4">
          <select
            className="form-select"
            value={selectedCommodity}
            onChange={(e) => setSelectedCommodity(e.target.value)}
          >
            <option value="">Select Commodity</option>
            {commodityList.map((commodity) => (
              <option key={commodity} value={commodity}>
                {commodity}
              </option>
            ))}
          </select>
        </div>
      </div>

      <div className="text-center mb-4">
        <button onClick={handleSearch} className="btn btn-success">
          🔍 Search
        </button>
      </div>

      {/* Best Mandi Recommendation */}
      {bestMandi && (
        <div className="alert alert-info text-center">
          <strong>💡 Best Place to Sell {bestMandi.commodity}: </strong>
          <br />
          {bestMandi.market} ({bestMandi.district}, {bestMandi.state}) <br />
          <strong>Modal Price: ₹{bestMandi.modal_price}</strong>
        </div>
      )}

      {/* Results */}
      {loading ? (
        <div className="text-center">Loading...</div>
      ) : mandiData.length > 0 ? (
        <div className="table-responsive">
          <table className="table table-bordered table-striped">
            <thead className="table-success">
              <tr>
                <th>State</th>
                <th>District</th>
                <th>Market</th>
                <th>Commodity</th>
                <th>Variety</th>
                <th>Min Price</th>
                <th>Max Price</th>
                <th>Modal Price</th>
                <th>Arrival Date</th>
                <th>📈 Trend</th>
              </tr>
            </thead>
            <tbody>
              {mandiData.map((item, idx) => (
                <tr key={idx}>
                  <td>{item.state}</td>
                  <td>{item.district}</td>
                  <td>{item.market}</td>
                  <td>{item.commodity}</td>
                  <td>{item.variety}</td>
                  <td>₹{item.min_price}</td>
                  <td>₹{item.max_price}</td>
                  <td>₹{item.modal_price}</td>
                  <td>{item.arrival_date}</td>
                  <td>
                    <button
                      className="btn btn-sm btn-primary"
                      onClick={() =>
                        handleTrendClick(
                          item.state,
                          item.market,
                          item.commodity
                        )
                      }
                    >
                      📈
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <div className="text-center text-muted">No data available.</div>
      )}

      {/* Trend Modal */}
      <Modal show={showModal} onHide={() => setShowModal(false)} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>📈 Price Trend (Past + Future)</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {trendData.history.length > 0 && (
            <Line
              data={{
                labels: [
                  ...trendData.history.map((h) => h.arrival_date),
                  ...trendData.prediction.map((p) => p.arrival_date),
                ],
                datasets: [
                  {
                    label: "Actual Prices",
                    data: trendData.history.map((h) => h.modal_price),
                    borderColor: "blue",
                    fill: false,
                  },
                  {
                    label: "Predicted Prices",
                    data: trendData.prediction.map((p) => p.modal_price),
                    borderColor: "green",
                    borderDash: [5, 5],
                    fill: false,
                  },
                ],
              }}
            />
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default MandiRates;
